.App {
    text-align: center;
    #quote-box {
        width: 500px;
        padding: 25px;
        background-color: white;
        border-radius: 10px;
        #quote-icon {
            padding-right: 15px;
        }
        #text {
            font-size: 30px;
        }
        #author {
            text-align: end;
            font-size: 20px;
        }
        .button {
            #tweet-quote {
                border-radius: 10px;
                float: left;
                cursor: pointer;
                color: white;
                font-size: 15px;
                padding: 10px;
                text-decoration: none;
            }
        }
        #new-quote {
            float: right;
            padding: 10px;
            font-size: 15px;
            border-radius: 10px;
            border: none;
            cursor: pointer;
        }
    }
    .App-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
    }
}